import { RendererCanvas } from './base/RendererCanvas';

export class Solution extends RendererCanvas {
    mv: Vector3 = new Vector3(0, 0, 0);
    t: number = 0.0;
    pts: Point[] = [];
    constructor(div: HTMLDivElement) {
        super(div);
        // this.canvas.style.background = '#fff';
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        // this.ctx.scale(2, 2);
        this.init();
    }
    public init() {
        this.start();
        this.pts.push(new Point(0, 0, 0))
    }
    public render(ctx: CanvasRenderingContext2D): void {
        ctx.fillStyle = '#f00';

        const g = -0.9

        this.pts.forEach((v) => {

            const dv = divide(this.mv, v.vec);
            const L = dv.length();
            dv.unitize();
            v.force.x = dv.x * Math.exp(-0.001 * L * L) * 0.1 + 0 * g;
            v.force.y = dv.y * Math.exp(-0.001 * L * L) * 0.1 + 1 * g;
            v.force.z = dv.z * Math.exp(-0.001 * L * L) * 0.1 + 0 * g;


            // v.force.y = -.0000095;
            v.move(0.9);
            v.render(ctx);
        });

        this.t += 0.1;

        // console.log(this.t);
    }
    public mouseMove(x: number, y: number): void {
        this.mv[0] = x - this.canvas.width * 0.5;
        this.mv[1] = y - this.canvas.height * 0.5;
    }

    public mouseClick(x: number, y: number): void {
        const v = new Point(x - this.canvas.width * 0.5, y - this.canvas.height * 0.5, 0);
        this.pts.push(v);
    }
}

class Point {
    public vec: Vector3;
    public velocity: Vector3;
    public force: Vector3;
    public mass = 1;
    public r: number = 10;
    public dt = 0.0;

    constructor(x, y, z) {
        this.vec =      new Vector3(x, y, z);
        this.velocity = new Vector3(0, 0, 0);
        this.force =    new Vector3(0, 0, 0);
    }
    public move(damping: number = 0.5) {
        // u *= damping;
        // u += f * (dt / m);
        // p += u * dt;

        this.velocity.x *= damping;
        this.velocity.y *= damping;
        this.velocity.z *= damping;


        this.velocity.x += this.force.x * (this.dt / this.mass);
        this.velocity.y += this.force.y * (this.dt / this.mass) - 0.9;
        this.velocity.z += this.force.z * (this.dt / this.mass);

        // if (this.vec.y < 0) {
        //     this.force.y *= -1.0
        // }

        this.vec.x += this.velocity.x * this.dt
        this.vec.y += this.velocity.y * this.dt 
        this.vec.z += this.velocity.z * this.dt


        
        if (this.vec.y < 0) {
            this.vec.y = 0.0
            // this.velocity.y = 0.0;

            if (this.velocity.y < 0.0) {
                this.velocity.y *= -1;
                this.force.y *= -1;
            }
            // this.force.y =-0.95 * this.force.y ;
        }

        // if (p.Z < 0.0) {
        //     p.Z = 0.0;
        //     if (u.Z < 0.0) u.Z = -u.Z;
        //   }


        // this.velocity.multiply(damping);
        // this.force.multiply(this.dt / this.mass)
        // this.velocity.add(this.force)
        // this.velocity.multiply(this.dt)
        // this.vec.add(this.velocity);


        this.dt += 0.1;
    }
    public render(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#f00';
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.r, 0, Math.PI * 2.0);
        ctx.closePath();
        ctx.fill();

        ctx.fillStyle = '#eee';
        ctx.fillText(`${this.vec.x} ${this.vec.y}`, this.vec.x - 10, this.vec.y - 15);
    }

}

const add = (a: Vector3, b: Vector3) => {
    return new Vector3(a.x + b.x, a.x + b.y, a.z + b.z);
}
const divide = (a: Vector3, b: Vector3) => {
    return new Vector3(a.x - b.x, a.x - b.y, a.z - b.z);
}
const multiply = (a: Vector3, b: number) => {
    return new Vector3(a.x * b, a.x * b, a.z * b);
}

class Vector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    constructor(x, y, z) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    public multiply(v: number) {
        this.x *= v;
        this.y *= v;
        this.z *= v;
    }
    public add(v: Vector3) {
        this.x += v.x;
        this.y += v.y;
        this.z += v.z;
    }
    public length() {
        const len = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        if (len < 0.00001) {
            return 0.00001;
        }
        return len;
    }
    public unitize() {
        let len: number = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        if (len < 0.0001) {
            return this;
        }
        this.x = this.x / len;
        this.y = this.y / len;
        this.z = this.z / len;
        return this;
    }
}
